.footer {
  background-color: $second-bg-color;
}
.footer__positioning {
  @media screen and (min-width: 768px) {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    padding: 0 36px;
  }
  @media screen and (min-width: 1280px) {
    padding: 0 114px;
    justify-content: space-evenly;
  }
}
.footer__title {
  margin-bottom: 43px;
  font-family: $second-font;
  text-transform: uppercase;
  font-weight: normal;
  @media screen and (min-width: 768px) {
    width: 340px;
    padding-bottom: 91px;
    margin-right: 40px;
  }
  @media screen and (min-width: 1280px) {
    width: 480px;
    padding-bottom: 97px;
    margin-right: 129px;
  }
}
.footer__title--secondary {
  font-size: 18px;
  line-height: 1.16;
  margin-bottom: 17px;
  width: 232px;
  color: $second-hero-bg-color;
  @media screen and (min-width: 768px) {
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    margin-top: 7px;
    margin-bottom: 16px;
    width: auto;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: 1.13;
    letter-spacing: 0.04em;
    margin-bottom: 15px;
    margin-top: 0;
  }
}
.footer__title--main {
  font-size: 34px;
  line-height: 1.15;
  letter-spacing: 0.04em;
  width: 200px;
  color: $text-title-color;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    letter-spacing: 0.06em;
    width: auto;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: 1.14;
  }
}
.footer__socials {
  // margin-bottom: 43px;
  @media screen and (min-width: 1280px) {
    margin-left: 129px;
  }
}
.social-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  margin-bottom: 46px;
}

.social-list__item {
  margin-right: 28px;
  padding-top: 4px;
}
.social-list__item:last-child {
  margin-right: 0;
}
.social-list__link {
  display: block;
  width: 32px;
  height: 32px;
  @media screen and (min-width: 768px) {
    width: 34px;
    height: 34px;
  }
}
.social-list__link:hover .social-icon,
.social-list__linkk:focus .social-icon {
  fill: $text-title-color;
  transform: scale(1.1);
}
.social-icon {
  width: 32px;
  height: 32px;
  fill: $socials-bg-color;
  @media screen and (min-width: 768px) {
    width: 34px;
    height: 34px;
  }
}
.footer__contacts {
  font-family: $main-font;
  font-weight: 900;
  font-size: 12px;
  line-height: 1.8;
  text-align: right;
  color: $main-text-color;
  @media screen and (min-width: 768px) {
    font-weight: 700;
    font-size: 16px;
    line-height: 1.9;
    letter-spacing: 0.02em;
    width: 268px;
  }
  @media screen and (min-width: 1280px) {
    font-weight: 500;
    font-size: 16px;
    line-height: 1.9;
  }
}

.footer__contact {
  display: flex;
  justify-content: flex-end;
  font-family: $main-font;
  font-weight: 700;
  font-size: 12px;
  line-height: 22px;
  text-align: right;
  color: $main-text-color;
  @media screen and (min-width: 768px) {
    font-weight: 700;
    font-size: 14px;
    line-height: 1.9;
  }
}
.footer__contact--tel {
  margin-left: 4px;
  color: $text-title-color;
}
.copyright {
  display: block;
}
.copyright__text {
  font-family: $main-font;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  color: $second-text-color;
  padding: 22px 91px 25px 79px;
  border-top: $quote-border;
  @media screen and (min-width: 768px) {
    line-height: 1.9;
    letter-spacing: 0.02em;
    padding: 29px 245px 18px 245px;
  }
  @media screen and (min-width: 1280px) {
    line-height: 1.9;
    letter-spacing: 0.02em;
    padding: 9px 502px 10px 502px;
  }
}
